import React, { useState, useEffect } from 'react';
import './Form.css';
import LogoGreen from '../../../svg/розеткаМонтажная область 1 копия 10.svg';

const Form = () => {
  const [phoneInputValue, setphoneInputValue] = useState('');

  const onChangePhoneValue = (e) => {};
  const [placeholderDomen, setplaceholderDomen] = useState(
    'http://Ваш поддомен.lp-crm.biz'
  );
  const domenInputHandler = () => {
    setplaceholderDomen('');
    sethttp('http://');
    setsrmbiz('.lp-crm.biz');
    setdomenClassName('placeholderDomen');
  };
  useEffect(() => {
    if (onChangeValue === null || onChangeValue === '') {
      setgreenRed('red');
    } else if (!onChangeValue.match(/^[a-zA-Z]+$/)) {
      setonChangeValue('');
      setdomenAccess('домен недоступен');
      setgreenRed('red');
    } else {
      setdomenAccess('домен доступен');
      setgreenRed('green');
    }
  });
  const onChangingDomenValue = (e) => {
    setonChangeValue(e.target.value);
  };
  const [http, sethttp] = useState('');
  const [srmbiz, setsrmbiz] = useState('');
  const [domenClassName, setdomenClassName] = useState('');
  const [domenAccess, setdomenAccess] = useState('');
  const [onChangeValue, setonChangeValue] = useState('');
  const [greenRed, setgreenRed] = useState('');
  return (
    <div class='form'>
      <div class='container'>
        <p class='h1CRMSystem'>CRM-система</p>
        <div className='flexingBox'>
          <div>
            <h3>для продаж в</h3>
          </div>
          <div className='greenLogo'>
            <img src={LogoGreen} alt='' />
          </div>
        </div>
        <p class='p1'>Зарегистрируйтесь и получите 14 дней</p>
        <p class='p2'>БЕСПЛАТНОГО тест-драйва сервиса!</p>
        <form>
          <label htmlFor='domen' className='http'>
            {http}
          </label>
          <label htmlFor='domen' className='srmbiz'>
            {srmbiz}
          </label>
          <label htmlFor='' className='labelAccess' style={{ color: greenRed }}>
            {domenAccess}
          </label>
          <input
            type='text'
            placeholder={placeholderDomen}
            className={domenClassName}
            value={onChangeValue}
            id='domen'
            style={{ color: greenRed }}
            onClick={domenInputHandler}
            onChange={onChangingDomenValue}
          />
          <input type='email' placeholder='Ваш Email' />;
          <input
            type='number'
            placeholder='Ваш телефон'
            value={phoneInputValue}
            onChange={onChangePhoneValue}
          />
          <input type='text' placeholder='Ваше имя' />
          <button type='submit'>Регистрация</button>
        </form>
      </div>
    </div>
  );
};
export default Form;
